.App {
  text-align: center;
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading-screen {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(
    90deg,
    rgba(201, 137, 211, 1) 0%,
    rgba(186, 167, 252, 0.2) 100%
  ) !important;

  display: grid;
  place-items: center;
}

.robo-logo {
  animation: App-logo-spin infinite 10s linear;
  font-size: 10rem;
}
