@import url(https://fonts.googleapis.com/css2?family=Turret+Road:wght@700&display=swap);
html {
  height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  font-family: 'Turret Road', cursive;
  font-weight: 700;
}

.bg-light {
  background: linear-gradient(
    90deg,
    rgba(201, 137, 211, 0.3) 0%,
    rgba(186, 167, 252, 0.5) 100%
  ) !important;
}

.logo {
  width: 200px;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading-screen {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(
    90deg,
    rgba(201, 137, 211, 1) 0%,
    rgba(186, 167, 252, 0.2) 100%
  ) !important;

  display: grid;
  place-items: center;
}

.robo-logo {
  animation: App-logo-spin infinite 10s linear;
  font-size: 10rem;
}

.FriendCodeContainer {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 13vw;
  transition: all 2s ease;
  height: 7vh;
}

.FriendCodeContainer[data-open='false']:hover {
  width: 15vw;
}

.FriendCodeContainer[data-open='true'] {
  width: 50vw;
  height: 50vh;
}

.FriendCodeContainerHeader {
  /* background: linear-gradient(
    90deg,
    rgba(201, 137, 211, 1) 0%,
    rgba(186, 167, 252, 1) 100%
  ) !important; */
  background-color: lightgrey;
  padding: 5px 0 0 10px;
  font-size: 23px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  height: 7vh;
  border-radius: 15px 0px 0px 0px;
  display: flex;
  align-items: flex-end;
}

.FriendContainerHeaderInterior {
  height: 90%;
  width: 100%;
  background-color: grey;
  border-radius: 15px 0px 0px 0px;
  padding-left: 5px;
}

.FriendContainerBody {
  background-color: #ebd5b3;
  height: 100%;
  margin-left: 10px;
}

.loader-holder {
  display: flex;
  justify-content: center;
  align-items: center;

  /* overflow: hidden; */
}

.loader {
  display: flex;
}

.circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;

  animation-name: loading;
  animation-duration: 0.6s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.circle:first-child {
  background-color: #a10702;
  animation-delay: 0s;
}

.circle:nth-child(2) {
  background-color: #f44708;
  animation-delay: 0.2s;
}

.circle:nth-child(3) {
  background-color: #faa613;
  animation-delay: 0.4s;
  margin-right: 0px;
}

@keyframes loading {
  from {
    transform: translateY(30px);
  }
  to {
    transform: translateY(0px);
  }
}

.flash-message {
  animation: FadeAnimation 3s ease-in 0.2s forwards;
}

@keyframes FadeAnimation {
  0% {
    opacity: 1;
    visibility: visible;
  }

  100% {
    opacity: 0;
    visibility: hidden;
  }
}

.FavoriteJobs {
  text-align: center;
}

.Profile {
  padding: 10px;
}

.wrapperClassName {
  width: 75%;
  margin: 1vh auto 0 auto;
}

.editorClassName {
