.newsletter {
  background-color: lightgrey;
  border-radius: 15px;
  border: 5px solid black;

  margin: 0 auto;

  height: 100%;
  width: 100%;

  text-align: center;
}

.newsletter .header {
  font-size: 1.5rem;

  font-style: italic;
